html{
    background-color: rgb(204, 204, 204);
}

body{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif
}

.textTitle{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    color: black;
    font-size: 45px;
    font-weight: bold;
    text-align: center;
    margin-top: 20px;
    text-decoration: none;
}

.app{
    display: block;
    width: 100%;
    height: 100%;
}

.header{
    display: flex;
    width: 100%;
}

.body{
    margin-top: 15px;
}

.footer{
    margin-left: 2rem;
}

.nav{
    width: 100%;
}

.navbar-ul{
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: 4rem;
    margin-right: 4rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: 0;
}

.navbar-a{
    display: flex;
    width: auto;
    min-width: 80px;
    height: auto;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    color: black;
    text-align: center;
    text-decoration: none;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 20px;
}

.navbar-a-active{
    background-color: black;
    color: white;
}

.navbar-a:hover{
    color: white;
    background-color: gray;
}

.div-lineamientos-page{
    display: flex;
    margin-left: 2rem;
    margin-right: 2rem;
    margin-top: 20px;
    background-color: white;
    padding-top: 20px;
    padding-bottom: 35px;
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}

.page-title{
    display: flex;
    flex-direction: row;
    font-size: 15px;
    margin: 0;
    margin-bottom: 10px;
    margin-top: 10px;
    text-align: left;
    font-weight: 300;
    width: 94%;
}

.container-lineamiento{
    display: flex;
    width: 47%;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    background-color: rgb(231, 231, 231);
    border-radius: 10px;
    margin-top: 10px;
    margin-left: 5px;
    margin-right: 5px;
}

.container-conditional-lineamiento{
    display: flex;
    width: 94%;
    justify-content: space-between;
    flex-direction: row;
    background-color: rgb(231, 231, 231);
    border-radius: 10px;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 1rem;
}

.conditional-lineamiento-div{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
}

.container-lineamiento-sox-required{
    background-color: rgb(255, 221, 221);
}

.container-lineamiento-required{
    background-color: rgb(214, 255, 213);
}

.text-lin{
    margin-left: 15px;
    font-weight: 450;
    font-size: 1.2rem;
}

.container-details-lineamiento{
    display: flex;
    margin-right: 15px;
    align-items: end;
    justify-content: center;
    flex-direction: column;
    font-weight: bold;
    color: rgb(145, 42, 42);
}

.lineamiento-details-text{
    display: flex;
    margin: 0;
}

.column-lineamientos{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
}

.div-conditional-lineamiento-condition{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 94%;
    margin-top: 1rem;
}

.conditional-lineamiento-condition-text{
    display: flex;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 0;
}